<template>
    <div id="container" :style="'width:'+width+';height:'+height">
        <img src="../assets/csdn.jpg"  alt="">
    </div>
</template>
<script>
export default {
    data() {
        return {
            width:0,
            height:0
        }
    },
    created() {
        this.width = this.getViewportSize().width;
        this.height = this.getViewportSize().height;
        console.log(this.width)
        console.log(this.height)
        setTimeout(()=>{
            this.$router.push("/index")
        },2000)       
    },
}
</script>
<style scoped>
    img{
        width: 100%;
        height:100%;
    }
</style>