<template>
    <div id="d5">
        <mt-header title="所有分类" fixed>
            <router-link to="/index" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
        <!-- <div class="clear"></div> -->
        <div id="d5_1">
            <img src="../../assets/add_person.png" alt="" class="img_1"><span class="span_1">我的分类</span>
        </div>
        <div class="f">
            <div style="color:red">
                推荐
            </div>
            <div style="color:red">
                动态
            </div>
            <div v-for="(item,i) of myClasses" :key="i" @click="del(i,item)">
                <span class="xx" >×</span>
                {{item}}
            </div>
            
        </div>
        <div id="d5_2">
            <img src="../../assets/add_person.png" alt="" class="img_1"><span class="span_1">更多分类</span>
        </div>
        <div class="f">
            <div v-for="(item,i) of allClasses" :key="i" @click="add(i,item)">
                <span class="xx">+</span>
                {{item}}
            </div>
        </div>
       
    </div>
</template>
<script>
export default {
    data(){
        return {
            arr:[],
            allClasses:[],
            myClasses:[]
        }
    },
    methods: {
        del(i,item){
            this.myClasses.splice(i,1)
            this.allClasses.push(item)
            this.updateClasses()
        },
        add(i,item){
            this.allClasses.splice(i,1)
            this.myClasses.push(item)
            this.updateClasses()
        },
        updateClasses(){
            if(this.$store.getters.getUser.uid==undefined){               
            return;
            }
            this.axios.get("/updateClasses",{params:{
                classes:this.myClasses.toString()
            }}).then(res=>{
                console.log(res);
            })
        }
    },
    
        
    
    created() {
        if(this.$store.getters.getUser.uid==undefined){               
            return;
        }else{
             this.axios.get("/class").then(res=>{
                this.myClasses = res.data.msg[0].classes.split(",");
                console.log(this.myClasses);
                this.allClasses = ["区块链","音视频开发","大数据","前端","Python","程序人生","数据库","游戏开发","移动开发","安全","研发管理","物联网","计算机基础","其他","运维","人工智能","java","架构"];
                console.log(this.myClasses);
                for(let i = 0; i < this.myClasses.length; i++){
                    for(let  j = 0; j < this.allClasses.length; j++){
                        if(this.myClasses[i] == this.allClasses[j]){
                            console.log(1);
                            this.allClasses.splice(j,1)
                        }
                    }
                }
                console.log(this.allClasses)
            });

        }
    },
}
</script>
<style scoped>
    .cs{
        width: 200 px;
        height: 200px;
        background: red;
    }
    .xx{
        display: block;
        width: 15px;
        height: 15px;
        position: absolute;
        top: -6px;left: 85%;
        background: #fff;
    }
    #d5_2{
        margin: 20px 0 10px;
        padding: 5px 10px;
    }
    #d5 .mint-header{
        background-color: transparent;
        color: black;
    }
    #d5_1{
        padding: 5px 10px;
        margin: 50px 0 10px;
    }
     .img_1{
        width: 30px;height: 30px;
        vertical-align: middle;
        margin-right: 15px;
    }
     .span_1{
        opacity: .7;
    }
    .f{
        display: flex;
        flex-wrap: wrap;
        padding: 0 15px;
        box-sizing: border-box
    }
    .f div{
        padding: 8px 2px;
        text-align: center;
        width: 22%;
        border: 1px solid rgba(0, 0, 0, .5);
        border-radius:5px; 
        margin: 5px 2px;
        font-size: 12px;
        font-weight: 800;
        position: relative;
    }
    
