<template>
    <div>
       <myHeader :isbar='isbar'  :title="title" :tab="tab" >
           
       </myHeader>
       <main>
           <div v-if="!write">
               <div v-if="list2.length>0">
                   <!-- <div v-if="list[0].type"> -->
                        <router-link :to="'/text/'+obj.tid" v-for="(obj,i) of list2" :key="i" >
                            <h5 class="fon">{{obj.title}}</h5>
                            <p>{{obj.content}}</p>
                        <div>
                            <span><table></table><img src="../../assets/icon_预览.png" alt="" class="np">{{obj.reading}}</span>
                            <span v-show="obj.star">star{{obj.star.split(',').length}}</span>
                        </div>
                        </router-link>
                   <!-- </div>                   -->
                    <van-cell is-link @click="showPopup">批量操作</van-cell>
                <van-popup v-model="show" position="bottom" class="vanPopup">
                    <div ref="cameraRemove" class="van-hairline--bottom" @click="close" showPhoto>
                    <!-- <input type="file" value="" id="choose" @change='onUpload' multiple> -->
                        <span>删除</span>
                    </div>
                    <div ref="multipleRemove" class="van-hairline--bottom" @click="close" showAlbum>
                        <span>转发</span>        
                    </div>
                </van-popup>
               </div>
               <div v-else>
                   <div id="kong"><img src="../../assets/kong.jpg" alt=""></div>
               </div>
               
           </div>
           <div v-else>
               <textarea rows="3" cols="12" v-model="msg" id="msg" placeholder="请输入要修改的内容"></textarea>
               <button id="update" @click="update">确认修改</button>
           </div>
       </main>
    </div>

</template>
<script>
import myHeader from './myheader'
export default {
    data() {
        return {
            title:[],
            isbar:'',
            tab:{code:"1"},
            list4:[],
            list1:[],
            list2:[],
            list3:[],
            list5:[],
            list6:[],
            list7:[],

            write:true,
            msg:"",
            show:false,
        }
    },
    created(){
        if(this.$store.getters.getUser.uid==undefined){
                history.go(-1);
                this.$messagebox.alert("请登录");
                
                return;
        }else{
        console.log(this.$route.query)
        var key = this.$route.query.key;
        var url = '/text';
        var url2 = '/follow'
        switch(key){

            case "concern":
            case "fans": {
                this.write=false;
                this.isbar = true;
                this.title = ["关注","粉丝"],
                this.axios.get(url2,{params:{
                    userid:this.$store.getters.getUser.uid
                }}).then(res=>{            
                    if(res.data.code=='1'){
                        this.list1 = res.data.msg 
                    }else{
                        console.log("获取失败")
                    }
                })
            };break;
            case "blog": {
                this.write=false;
                this.isbar = true;
                this.title = ["博文","分类专栏"],
                this.axios.get(url,{params:{
                    userid:this.$store.getters.getUser.uid,
                    type:10
                }}).then(res=>{
                    if(res.data.code=='1'){
                        this.list2 = res.data.msg 
                        console.log(this.list2)
                    }else{
                        console.log("获取失败")
                    }
                })
            };break;
            case "Blink": {
                this.write=false;
                this.isbar = false;
                this.title = ["Blink"],
                this.axios.get(url,{params:{
                    userid:this.$store.getters.getUser.uid,
                    type:30
                }}).then(res=>{
                    if(res.data.code=='1'){
                        this.list3 = res.data.msg 
                        console.log(this.list3)
                    }else{
                        console.log("获取失败")
                    }
                })
            };break;
            case "comment": {
                this.write=false;
                this.isbar = true;
                this.title = ["发帖","回帖"],
                this.axios.get(url,{params:{
                    userid:this.$store.getters.getUser.uid,
                    type:20
                }}).then(res=>{
                    if(res.data.code=='1'){
                        this.list4 = res.data.msg 
                        console.log(this.list4)
                    }else{
                        console.log("获取失败")
                    }
                })
            };break;
            case "label": {
                this.write=false;
                this.isbar = false;
                this.title = ["我的书架"],
                this.axios.get(url2,{params:{
                    userid:this.$store.getters.getUser.uid
                }}).then(res=>{
                    console.log(res);
                    if(res.data.code=='1'){
                        this.list5 = res.data.msg[0].books.split(',')
                        console.log(this.list5); 
                    }else{
                        console.log("获取失败")
                    }
                })
            };break;
            case "star": {
                this.write=false;
                this.isbar = false;
                this.title = ["我的收藏夹"],
                this.axios.get(url2,{params:{
                    userid:this.$store.getters.getUser.uid
                }}).then(res=>{
                    if(res.data.code=='1'){
                        this.list6 = res.data.msg[0].collects.split(',') 
                    }else{
                        console.log("获取失败")
                    }
                })
            };break;
            case "underway": {
                this.write=false;
                this.isbar = false;
                this.title = ["我的足迹"],
                this.axios.get(url2,{params:{
                    userid:this.$store.getters.getUser.uid
                }}).then(res=>{
                    if(res.data.code=='1'){
                        this.list7 = res.data.msg[0].books.split(',') 
                    }else{
                        console.log("获取失败")
                    }
                })
            };break;
            case "username": {
                this.write=true;
                this.isbar = false;
                this.title = ["昵称"]                
            };break;
            case "sign": {
                this.write=true;
                this.isbar = false;
                this.title = ["签名"]
            };break;
            case "school": {
                this.write=true;
                this.isbar = false;
                this.title = ["学校"]
            };break;
            case "company": {
                this.write=true;
                this.isbar = false;
                this.title = ["公司"]
            };break; 
            default:
                    break;          
        }
        console.log(this.write)

        }
        

        

    },
    components:{
        myHeader,
    },
    methods: {
        gettab(tab){
            console.log(tab)
        },
        update(){
            var obj = this.$store.getters.getUser;
            switch (this.title) {
                case '昵称':{obj.username=this.msg}break;
                case '签名':{obj.sign=this.msg}break;
                case '学校':{obj.school=this.msg}break;
                case '公司':{obj.company=this.msg}break;
            
                default:
                    break;
            }
            this.axios.get('/update',{
                params:obj
            }).then(res=>{
                if(res.data.code=="1"){
                    this.$store.commit('saveUser',res.data.msg)
                }else{
                    console.log("修改失败")
                }
            })
            
        },
        showPopup(){
            this.show =true;
        },
        close(){
            this.show=false;
        },
    },
}
</script>
<style scoped>
    #msg{
        width: 100%;
        height: 8rem;
        border: none;
    }
    #update{
        width: 100%;
        height: 2.5rem;
        background-color: #6cf;
        border-radius: 0.5rem;
        border:none;
        outline: none;
        color: #fff;
        font-size: 1.25rem;
        margin: 0.5rem auto;
    }
    #kong {
        width: 100%;
        margin: 5rem auto;
        text-align: center;
    }
     .fon{
        font: 17px "微软雅黑";
        font-weight: 800;
    }
    a>div .n{
        color:blue;
    }
    .np{
        width: 15px;
        box-sizing: border-box;
        position: relative;
        top: 3px;
    }
    a:visited{
        color:rgba(00,00 ,1, 0.7);
    }
    a{
        display: block;
        text-decoration: none;
        color: black;
        padding: 10px;
        border-bottom:0.5px solid slategray 
    }
    a>h5,#d3>a>p{
        margin:5px 0;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    a>p{
        font: 12px "微软雅黑";
        color: rgba(00,00 ,1, 0.7);
    }
    a>div>span{
        display: inline-block;
        margin-right:15px;
        font-size:8px;
        color: rgba(00,00 ,1, 0.7);
        line-height: 20px;
        box-sizing: border-box;
        /* padding-top:2px;  */
    }
    a>div>div img{
        width: 20px;
        vertical-align: middle;
    }
    a>div>div{
        width: 20px;
        height: 20px;
        border-radius:50%; 
        display: inline-block;
        overflow: hidden;
        border: 1.5px solid #ffffff;
        vertical-align: middle;
    }
     .van-popup div{
        width: 100%;
        height: 33%;
        text-align: center;
        font-size: 1.25rem;
        line-height: 4.5rem;
    }
</style>
