<template>
    <div>
        <myHeader :isbar='isbar'  :title="title" :tab="tab" >           
       </myHeader>
        <table>
            <tr>
                <td> <input type="text" placeholder="请输入用户名" v-model="uname"></td>
            </tr>
            <tr>
                <td><input type="text" v-model="yzm" placeholder="请输入验证码"><span @click="getWord" >获取验证码</span> </td>
            </tr>
            <tr>
                <td> <input type="password" placeholder="密码为11~20位字母数字组合" v-model="upwd"></td>
            </tr>
        </table>
        <button id="reg" @click="reg">注 册</button>
        <div id="yuan"><img src="../../assets/程序猿.jpg" alt=""></div>
    </div>
</template>
<script>
import myHeader from './myheader'
export default {
    data() {
        return {
            title:["注册账户"],
            isbar:false,
            tab:{code:1},
            uname:"",
            upwd:"",
            yzm:""
        }
    },
    components:{
        myHeader,
    },
    methods:{
        reg(){
            if(!/^\w+$/.test(this.uname)){ this.$messagebox.alert("用户名格式错误");return}
            if(!/^\d+$/.test(this.upwd)){this.$messagebox.alert("密码格式错误");return}
            this.axios.get('reg',{
                params:{
                    uname:this.uname,
                    upwd:this.upwd 
                }
            }).then(result=>{
                console.log(result);
                if(result.data.code == 1){
                    this.$messagebox.alert("注册成功")
                    console.log(result)
                    this.$router.push('/login')
                }else{
                    this.$messagebox.alert("注册失败")
                }
            })
        },
        getWord(){},
    }
}
</script>
<style scoped>
    table{
        width: 80%;
        height: 9rem;
        margin: 3rem auto 1rem;
        border-radius: 0.75rem;
        background-color:#fff;
        position: relative;
    }
    table td input{
        width: 90%;
        height: 2.5rem;
        padding: 0 0.5rem;
        border: 0.5px solid #ccc;
        border-radius: 0.5rem;
    }
    table td span{
        position: absolute;
        right: 1rem;
        top:3.75rem;
        color:deepskyblue;
    }
    #reg{
        width: 80%;
        height: 2.5rem;
        margin: 0.5rem auto ;
        background-color: rgb(12, 147, 224);
        border: 0.5px solid #ccc;
        border-radius: 0.5rem; 
        color: #fff;
    }
    #yuan{
        width: 80%;
        margin: 3rem auto;
    }
</style>