<template>
    <div id="d3" style="text-align:left">
        <router-link :to="'/text/'+obj.tid" v-for="(obj,i) of arr3" :key="i" >
            <h5 class="fon">{{obj.title}}</h5>
            <p>{{obj.content}}</p>
            <div>
                <div><img :src="obj.headpic" alt=""></div>
                <span class="n">
                    <!--  -->
                    {{obj.username}}
                </span>
                <span>
                    <table></table>
                    <img src="../../assets/icon_预览.png" alt="" class="np">
                    {{obj.reading}}
                </span>
                <span>
                    star
                    {{obj.star.split(',').length}}
                </span>
            </div>
        </router-link>
        <button @click="add" class="btn">
            加载更多
        </button>
    </div>
</template>
<script>
//var a=888;
export default {
    props:["classes"],
    data(){
        return {
            arr:[],
            user:[],
            arr2:[],
            arr3:[],
            pno:1,
            follower:[],
            status:1
        }
    },
    methods: {
        add(){
            this.pno+=1;
            var status = this.load();
            if(this.status == -1){
                this.$messagebox.alert("没有更多了!");
            }
        },
        load(){
            this.arr2=[];
            var obj = {};
            if(this.classes == "all"){
                obj = {params:{
                    pno:this.pno
                }}
            }else if(this.classes == "concern"){
                if(this.$store.getters.getUser.uid==undefined){
                    return;
                }else{
                    this.axios.get("/v1/follow",{params:{
                    }}).then(res=>{
                        console.log(res);
                        if(res.data.code!=-1){
                            if(res.data[0].follower!=""){
                                this.follower=res.data[0].follower.split(",")
                                console.log(this.follower)
                                obj = {params:{
                                    pno:this.pno,
                                    follower:this.follower.toString()
                                }}
                            }                       
                        }                   
                    })
                }                
            }else{
                obj = {params:{
                    pno:this.pno,
                    classes:this.classes
                }}
            }
            this.axios.get('/v1/text',obj).then((result)=>{
                this.arr=result.data.data;
                //console.log(this.arr);
                if(result.data.msg==0){
                    this.status = -1
                    
                }else{
                    for(var i=0;i<this.arr.length;i++){
                        this.arr2.push(this.arr[i].userid);
                        // this.arr[i].headpic="";
                        // this.arr[i].username=""; 
                    }
                    this.userload();
                }
            })
        },
        userload(){
            //console.log(this.userid)
            this.axios.get('/v1/user',{params:{
                userid:this.arr2.toString()
            }}).then((result)=>{
                this.user=result.data;
            }).then(res=>{ 
                //console.log(this.user)
                for(var i=0;i<this.arr.length;i++){
                    //console.log(this.arr[i].userid)                       
                    for(var a=0;a<this.user.length;a++){
                        if(this.arr[i].userid==this.user[a].uid){
                            this.arr[i].headpic=this.user[a].headpic;
                            this.arr[i].username=this.user[a].username;
                        }
                    }
                   
                }
                this.arr3=this.arr3.concat(this.arr);
                console.log(this.arr)
            }) 
        }
    },
    created(){
        
            this.load();
        
        
    }
}
</script>
<style scoped>
    .btn{
        outline: none;
        border: 0;
        width: 100%;
        height: 50px;
        background: rgb(80, 133, 168)
    }
    .fon{
        font: 17px "微软雅黑";
        font-weight: 800;
    }
    a>div .n{
        color:blue;
    }
    .np{
        width: 15px;
        box-sizing: border-box;
        position: relative;
        top: 3px;
    }
    a:visited{
        color:rgba(00,00 ,1, 0.7);
    }
    a{
        display: block;
        text-decoration: none;
        color: black;
        padding: 10px;
        border-bottom:0.5px solid slategray 
    }
    a>h5,#d3>a>p{
        margin:5px 0;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    a>p{
        font: 12px "微软雅黑";
        color: rgba(00,00 ,1, 0.7);
    }
    a>div>span{
        display: inline-block;
        margin-right:15px;
        font-size:8px;
        color: rgba(00,00 ,1, 0.7);
        line-height: 20px;
        box-sizing: border-box;
        /* padding-top:2px;  */
    }
    a>div>div img{
        width: 20px;
        vertical-align: middle;
    }
    a>div>div{
        width: 20px;
        height: 20px;
        border-radius:50%; 
        display: inline-block;
        overflow: hidden;
        border: 1.5px solid #ffffff;
        vertical-align: middle;
    }
</style>