<template>
    <div id="d2">
        <div id="d2_1" class="d2_1" >
            <div style="width:1.5rem;" @click="go"><img src="../../assets/后退.png" style="width:100%; margin-top:0.8rem;" alt=""></div>
            <mt-search class="" v-model="value" cancel-text="取消" placeholder="" :attr="{autofocus:true}"></mt-search>
        </div>
        <div v-show="value==''?true:false">
            <span>热门搜索</span>
            <div id="d2_2">
                <div v-for="(el,i) of arr" :key="i">{{el}}</div>
            </div>
        </div>
        <div v-show="value==''?false:true" class="table" @click="rou">
            <div class="father" v-for="(obj,i) of arr2" :key="i" :data-tid="obj.tid">
                <div class="title" :data-tid="obj.tid">{{obj.title}}</div>
                <div class="content" :data-tid="obj.tid">{{obj.content}}</div>
                <div class="actor" :data-tid="obj.tid">阅读量：{{obj.reading}}</div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return { 
            value:"",
            arr:["JAVAscript","python","今晚吃什么","爱情36计","前端大全","皇家马德里"],
            bol:true,
            arr2:[]
        }
    },
    methods:{
        go(){
            window.history.go(-1);
        },
        show(){
            this.bol=false;
        },
        rou(e){
            if(e.target.nodeName=="DIV"){
                var tid=e.target.dataset.tid;
                //console.log(tid);
                this.$router.push("/text/"+tid) 
            }
           
        }
    },
    watch: {
        value(){
            // console.log(1);
            if(this.value==""){
                return;
            }else{
                this.axios.get('/v1/seach',{
                    params:{
                        cont:this.value
                    }
                }).then(res=>{
                    //console.log(res);
                    this.arr2=res.data;
                    console.log()
                })
            }
        }
    },
}
</script>
