    .d2_1{
        display: flex;
    }
    
    .btn1{
        background-color: rgba(00, 00, 00, 0.1);
        width: 85%;
        display: flex;
        line-height: 30px
    }
    #d2_1>div:first-child{
        font-size:24px;
        line-height: 52px;
        font-weight: bolder;
        margin-left: 5px;
        height: 52px;
    }
    .mint-search{
        height: 52px;
    }
    span{
        padding: 5px 20px;
        color:black;
        font-size: 18px;
        font-weight: bold;
        margin-top: 5px;
        display: block;
        text-align: left
    }
    #d2_2{
        display: flex;
        flex-wrap: wrap; 
        /* margin-top: 5px; */
        padding: 10px;
    }
    #d2_2>div{
