<template>
    <div id="container">
        <myHeader :isbar='isbar'  :title="title" :tab="tab"></myHeader>
        <van-cell-group>
             <van-cell title="用户服务条款" value="" is-link to="/rules"/>
             <van-cell title="隐私条款" value="" is-link to="/conceal"/>
             <van-cell title="版本" value="v1.0.0"/>
        </van-cell-group>

        <button v-if="!islogin" @click="tologin">您尚未登录</button>
        <button v-else @click="bye">退出当前账号</button>
    </div>
</template>
<script>
import myHeader from './myheader'
export default {
    data() {
        return {
            title:["设置"],
            tab:{code:1},
            isbar:false,
            islogin:false
        }
    },
    created() {
        if(this.$store.getters.getUser.uid==undefined){
                this.islogin=false;
                return;
        }else{
            this.islogin = true;
        }
    },
    methods: {
        tologin(){
            this.$messagebox.confirm("是否前往登陆？").then(res=>{
                this.$router.push("/login")
            }).catch(err=>{
                return
            })
        },
        bye(){
            this.$messagebox.confirm("是否要退出当前账号？").then(res=>{
                this.axios.get("/bye").then(res=>{
                    this.$store.commit("clearUser")
                    this.$messagebox.alert("退出成功");
                    this.$router.push("/login")
                })
            }).catch(err=>{
                return;
            })            
        }
    },
    components:{
        myHeader,
    }
}
</script>
<style scoped>
    .van-cell-group{
        margin-top: 1rem;
        border-top:0.5px solid #ccc;
        border-bottom:0.5px solid #ccc;
        box-shadow: 0 1rem 1rem #eee; 
        text-align: left;
    }
    button{
        margin-top: 2rem;
        width: 100%;
        height:2rem;
        outline: none;
        border: none;
        border-radius: 0.5rem;
        background-color:red;
        color:#fff;
    }
</style>