<template>
    <div id="d4">
         <mt-header :title="obj.title"  fixed>
            <router-link to="/index" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
            <mt-button icon="more" slot="right" @click="showPopup"></mt-button>
           <div></div>
        </mt-header>
        <div class="hei">
            <h2>
                {{obj.title}}
            </h2>
        </div>
        <div class="buzhidao">
            <span>{{t}}</span>
            <span>|</span>
            <span>预计阅读时间3分钟</span>
        </div>
        <div class="nav">
            <div>
            <img :src="user.headpic" alt="" class="hp">
                <p>{{user.username}}</p>
            </div>
            <div>
            <mt-button>私信</mt-button>
            <mt-button @click="gz" :class="{'gz':g}" id="btn" v-html="chuo">关注</mt-button>
            </div>
        </div>
        <div class="body">
            {{content}}
        </div>
        <div class="gg">

        </div>
        <div class="time"> 
            <span>文章最后发布于：</span>
            <span>{{time}}</span>
        </div>
        <div class="zan">
            <div>
                <ul>
                    <li v-for="(obj,i) of pic" :key="i"><img :src="obj.headpic" alt="" class="hp"></li>
                </ul>
            </div>
            <div>
                {{obj.star.split(',').length+"人表示很赞"}}
            </div>
            <div class="z" :class="{'nb':i}" @click="dz">
                <img src="../../assets/点赞_line.png" alt="" class="dz">
                <!-- <img src="" alt=""> -->
            </div>
        </div>
        <div class="a"></div>
        <div class="pl">
            <div>评论</div>
            <div class="flex" v-for="(obj,i) of pl2" :key="i">
                <img src="../../assets/logo.png" alt="">
                <div>
                    <div>
                        <h5>A973024191</h5>
                        <p>{{obj.time}}</p>
                    </div>
                    <div class="p">
                        <p>{{obj.content}}</p>
                    </div>
                </div>
            </div>
            <div>
                <button>查看剩余评论</button>
            </div>
        </div>
        <div class="req">
            <span>评论</span>
            <input type="text" v-model="req" placeholder="想对Ta说点什么...">
            <button @click="go">发送</button>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            //控制点赞 点亮图标
            i:false,
            //文章内容
            obj:{star:"1,2"},
            //该文章ID
            tid:"",
            //发布时间1
            time:"",
            //发布时间2
            t:"",
            //用户ID
            userid:0,
            //发表该文章的用户信息
            user:{},
            //控制关注
            g:false,
            //关注BUTTON内容
            chuo:"关注",
            //
            arr:[],
            //点赞用户列表
            pic:[],
            //
            arr2:[],
            //
            str:"",
            //评论内容双向绑定
            req:"",
            //评论1 介质
            pl1:[],
            //评论2
            pl2:[],
            //
            time2:"",
            content:"",
            //当前登录用户
            nowUser:{},
            isHandle:false,
            show:false
        }
    },
    methods: {
        showPopup(){
            this.show =true;
        },
        close(){
            this.show=false;
        },
        //评论模块 载入数据进库
        go(){
            if(this.$store.getters.getUser.uid==undefined){
                this.$messagebox.alert("请登录");
                this.$router.push("/login")
                return;
            }
            var time=new Date();
            time=time.getTime();
            this.axios.get('/v1/pl2',{params:{
                textid:this.tid,
                content:this.req,
                time,
                parentid:this.tid
            }}).then(res=>{
                console.log(res);
                this.req="";
                this.rs();
            })
        },
        //读取该文章下的品论
        rs(){
            this.axios.get('/v1/pl',{
                params:{
                    tid:this.tid
                }
            }).then(res=>{
                console.log(res);
                this.pl1=res.data;
                function d(oldT){
                    var t1=new Date();
                    var t=t1.getTime()-oldT;
                    t=parseInt(t/1000/60/60);;
                    if(1<=t&&t<=24){
                        return t+"小时前";
                    }else if(t<1){
                        return "刚刚发送";
                    }else if(t>24){
                        return parseInt(t/24)+"天前"
                    }else if(t>720){
                        return parseInt(t/720)+"月前"
                    }
                }
                var pl1=this.pl1
                for(var i=0;i<pl1.length;i++){
                    pl1[i].time=d(pl1[i].time);
                }
                console.log(pl1)
                this.pl2=pl1;
                console.log(this.pl1)
                console.log(this.pl2)
            })
        },
        //动态获取关注信息 查询登录的用户是否给该文章用户关注
        gz(){
            this.g=!this.g;
            if(this.g){
                this.chuo="已关注";
                this.arr.push(this.user.uid);
                console.log(this.user.uid);
                this.up();
                this.axios.get("/v1/follow",{params:{
                }}).then(res=>{
                    this.arr=res.data[0].follower.split(",")
                    console.log(this.arr)
                })
                // function up(){
                //     this.axios.get('/update',{
                //         params:{
                //             fl:this.arr.toString()
                //         }
                //     })
                // }
                
            }else{
                this.chuo="关注";
                for(var i=0;i<this.arr.length;i++){
                    console.log(this.user.uid)
                    if(this.arr[i]==this.user.uid){
                        this.arr.splice(i,1);
                        console.log(this.user.uid)
                        //console.log(this.arr);
                        this.up();
                        this.axios.get("/v1/follow",{params:{
                        }}).then(res=>{
                            this.arr=res.data[0].follower.split(",")
                            console.log(this.arr)
                        })
                    }
                }
            }
        },
        //点赞功能
        dz(){
            this.i=!this.i;
            if(this.i){
                //this.i=!this.i;
                this.arr2=this.obj.star.split(',');
                this.arr2.push(this.user.uid);
                console.log(this.arr2)
                this.z();
                this.d();
            }else{
                //this.i=!this.i;)
                // var str=this.obj.star.split(",")
                this.arr2 =this.obj.star.split(",");
                for(var i=0;i<this.arr2.length;i++){
                    if(this.arr2[i]==this.user.uid){
                        this.arr2.splice(i,1);
                        console.log(this.arr2)
                        this.z();
                        this.d();
                        break ;
                    }
                }
                
            }
        },
        //跳转页面 直接加载文章内容数据
        load(){
            this.axios.get('/v1/usertext',{params:{tid:this.tid}}).then((result)=>{
                console.log(this.tid)
                this.content = result.data[0].content
                this.obj=result.data[0];
                this.time=result.data[0].time;
                this.userid=result.data[0].userid;
                var t1=new Date();
                var t=t1.getTime()-this.time;
                t=parseInt(t/1000/60/60);
                if(1<=t&&t<=24){
                    this.t=t+"小时前";
                }else if(t<1){
                    this.t="刚刚发送"
                }else if(t>24){
                    this.t=parseInt(t/24)+"天前"
                }else if(t>720){
                    this.t=parseInt(t/720)+"月前"
                };
                this.time=new Date(this.time);
                this.time=this.time.toLocaleDateString();
                this.userload();
                this.d();               
                //console.log(this.arr[0].star.split(',').length);
            })
        },
        //加载写该文章的用户数据
        userload(){
            this.axios.get('/v1/user',{params:{
                userid:this.userid
            }}).then((result)=>{
                this.user=result.data[0];
                var arr2=this.obj.star.split(",");
                for(var num of arr2){
                    if(num==this.user.uid){
                        this.i=true
                    }
                };
                //加载该文章用户数据后再请求登录用户列表
                this.after();
            })
        },
        //请求登录用户的关注列表，对比自己的关注列表里有没有该文章用户的ID
        after(){
            this.axios.get("/v1/follow",{params:{
            }}).then(res=>{;
                this.arr=res.data[0].follower.split(",")
                //循环检测是否有该文章用户ID
                for(var num of this.arr){
                    if(num==this.user.uid){
                        this.chuo="已关注";
                        this.g=true
                    }else{
                        this.chuo="关注";
                        this.g=false
                    }
                }
            })
        },
        //关注被修改后，又请求更新数据库
        up(){
            this.axios.get('/v1/update',{
                params:{
                    fl:this.arr.toString()
                }
            })
        },
        //点赞用户的头像
        d(){
            this.str=this.obj.star;
            var arr;
            if(this.arr2.length==0){
                 arr=this.str;
            }else{
                arr=this.arr2.toString();
            }
            this.axios.get('/v1/user',{
                params:{
                    userid:arr
                }
            }).then(res=>{
                //console.log(this.str)
                this.pic=res.data;
                this.pic=this.pic.slice(-6);
            })
        },
        //修改点赞后，同时把新的点赞用户数和用户ID传入数据库
        z(){
            this.axios.get('/v1/update2',{
                params:{
                    star:this.arr2.toString(),
                    tid:this.tid
                }
            }).then(res=>{
                this.axios.get('/v1/usertext',{params:{tid:this.tid}}).then((result)=>{this.obj=result.data[0];
                }
                )
            })
        },
        handle(){

        }
    },
    //组件虚拟DOM数加载过后调用的函数
    mounted(){
        this.tid=this.$route.params.tid;
        this.load();
        this.rs();
        if(this.$store.getters.getUser.uid!=undefined){
            this.nowUser = this.$store.getters.getUser;
        }
        if(this.nowUser.uid == this.user.id){
            this.isHandle = true
        }
    }
}
</script>
<style scoped>
