        display: flex;
        align-items: center;
        padding: 2px 5px;
        height: 40px;
        width: 100%;
        box-sizing: border-box
    }
    .req>span{
        display: block;
        padding: 5px;
        text-align: center;
        margin-right: 10px
    }
    .req>input{
        padding: 5px 8px;
        border-radius:5px;
        border: 0;
        outline: none;
        background: rgba(84, 84, 87, 0.5);
        font-size: 12px; 
    }
    .req>button{
        border: 0px;
        outline: none;
        border-radius:3px; 
        padding: 5px;
        font-size:12px;
        margin-left: 10px
    }
    .gz{
        /* border: #fff solid 1px ; */
        background: #fff !important;
        color: cornflowerblue !important;
        padding: 0px !important;
    }
    .zan .z.nb{
        background: rgba(199, 49, 49,.9);
