        border: 1px solid red;
        margin: 0 10px
    }
    #d4 .nav p{
        margin:0  0 0 15px ;
        display: inline-block;
    }
    .nav img{
        vertical-align: middle;
    }
    #d4 .body{
        margin-top: 15px;   
        height: 1000px;
        border: 1px solid red;
        padding: 5px 10px;
    }
    #d4 .gg{
        margin: 10px 0;
        padding: 0 10px;
        height: 80px;
        border: 1px solid red
    }
    #d4 .time{
        text-align: right;
        padding: 5px 10px;
        color:rgba(84, 84, 87, 0.7);
    }
    #d4 .zan{
        margin: 10px 0;
        padding:0 10px;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    .zan ul{
        /* position: relative; */
        display: flex;
        justify-content:center;
        padding: 0;list-style: none;
        width: 150px;
    }
    /* .zan ul>li{
        /* width: 30px;
        height: 30px;
        border-radius:50%; 
        background: transparent;
        border: 2px solid #fff;
        overflow: hidden;
        text-align: center;
        box-sizing: border-box;
         margin-right:-5px; */
        /* position: absolute;
        left: 2px 
    } */
    /* .zan ul>li:not(:last-child){
       margin-left: -5px;
    } */
    .zan .z{
        width: 40px;
        height: 40px;
        border-radius:50%;
        background-color:rgba(84, 84, 87, 0.1);  
        text-align: center;
        vertical-align: middle
    }
    #d4 .a{
        width: 100%;
        height: 20px;
        background: burlywood;
    }
    #d4 .pl{
        display: flex;
        flex-direction: column;
        padding:5px 10px;
    }
    .pl h5,.pl p{
        margin: 0;
        margin-bottom:5px 
    }
    .pl img{
        display: block;
        width: 40px;
