        margin-right: 10px
    }
    .flex{
        display: flex;
        margin:10px 0; 
    }
    .flex>div>div>p{
        font-size: 12px;
        color: rgba(84, 84, 87, 0.7);
    }
    #d4 .p{
        margin-top: 10px;
        
    }
    #d4 .p>p{
        font-size: 15px;
        font-weight: 600;
        color: #000;
    }
    .flex>div{
        margin-top:5px; 
    }
    .pl>div:last-child button{
        margin:10px auto 0; 
        border-radius:20px;
        border: 1px solid blue ;
