import Vue from 'vue'
import Router from 'vue-router'
import Center from './components/common/center.vue'
import User from './components/common/user.vue'
import Login from './components/common/login.vue'
import Reg from './components/common/register.vue'
import Rules from './components/common/rules.vue'
import Conceal from './components/common/conceal.vue'
import List from './components/common/list.vue'
import Index from './components/index.vue'
import Start from './components/start.vue'
import Edit from './components/common/edit.vue'

import text from './components/home/text.vue'
import Fl from './components/home/fl.vue'
import Search from './components/home/seach.vue'

import detail from './components/study/details'
import myxq from './components/comment/Myxq.vue'
import set from './components/common/set.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/seach',component: Search},
    {path: '/fl',component: Fl},
    {path: '/text/:tid',component: text},
    {path: '/details',component: detail},
    {path: '/Myxq/:tid',component: myxq},
    {path: '/',component: Start},
    {path: '/index',component: Index},
    {path: '/edit',component: Edit},
    {path: '/user',component: User},
    {path: '/login',component: Login},
    {path: '/reg',component: Reg},
    {path: '/rules',component: Rules},
    {path: '/conceal',component: Conceal},
    {path: '/list',component: List},
    {path: '/set',component: set},

  ]
})
