<template>
    <div>
        <myHeader :isbar='isbar'  :title="title" :tab="tab"></myHeader>
        <main>
            <div id="head">
                <input type="file" name="file" id="file_more1" value="" accept="image/jpeg,image/png,image/jpg,image/gif" multiple="" @change="previewImg(this);">
                <img :src="pic" alt="">
                <van-cell is-link @click="showPopup">更 改</van-cell>
                <van-popup v-model="show" position="bottom" class="vanPopup">
                    <div ref="cameraRemove" class="van-hairline--bottom" @click="close" showPhoto>
                    <!-- <input type="file" value="" id="choose" @change='onUpload' multiple> -->
                        <span>拍照</span>
                    </div>
                    <div ref="multipleRemove" class="van-hairline--bottom" @click="close" showAlbum>
                        <label>
                            <input id="testFile" type="file" accept="image/*"  ref="avatarInput" @change="changeImage($event)">
                            <span>从相册中选择</span>
                        </label>
        
                    </div>
                </van-popup>
            </div>
            <van-cell title="昵称" to="/list?key=username" is-link :value="this.$store.getters.getUser.username"></van-cell>
            <van-cell title="简介" to="/list?key=sign" is-link :value="this.$store.getters.getUser.sign"></van-cell>
            <van-cell title="学校" to="/list?key=school" is-link :value="this.$store.getters.getUser.school"></van-cell>
            <van-cell title="公司" to="/list?key=company" is-link :value="this.$store.getters.getUser.company"></van-cell>
        </main>
    </div>
</template>
<script>
import myHeader from './myheader'
export default {
    data() {
        return {
            headpic:{default:"../../assets/程序猿.jpg"},
            isbar:false,
            title:["编辑资料"],
            tab:1,
            show:false,
            pic:''
        }
    },
    created(){
        console.log(this.$store.getters.getUser)
        // this.pic=require(`${this.$store.getters.getUser.headpic}`)
        this.pic=this.$store.getters.getUser.headpic;
    },
    methods: {
        showPopup(){
            this.show =true;
        },
        close(){
            this.show=false;
        },
        changeImage(e){
            let file = e.target.files[0];
            console.log(this,file);

            let param = new FormData();
            param.append('logo',file,file.name)
            param.append('type','1');
            console.log(param.get('logo'))

            let config = {
                headers:{'Content-Type':'multipart/form-data'}
            }
            this.axios.post('/avatar',param,config).then(res=>{
                console.log(res)
                if(res.data.code == '1'){
                    console.log("上传成功")
                    this.$store.commit('update',res.data.msg)
                    this.pic=this.$store.getters.getUser.headpic
                }else{
                    console.log("上传失败")
                }
            })
        },
        

    },
    components:{
        myHeader,
    }
}
</script>
<style scoped>
    main{
        text-align: left;
    }
    #head{
        width: 8rem;
        height: 8rem;
        margin: 2rem auto;
        /* padding: 2rem; */
        border-radius: 50%;
        overflow: hidden;
        background-color:#aaa;
        position: relative;
    }
    #head img{
        width: 100%;
    }
    #file_more1{
        display: none;
    }
    #head .van-cell.van-cell--clickable{
        width: 100%;
        height: 50%;
        padding: 0;
        text-align: center;
        line-height: 3rem;
        color:#fff;
        font-size: 1.25rem;
        position: absolute;
        top:60%;
        background-color:rgba(0,0,0, 0.6);
        z-index: 999;
    }
    .van-cell__value--alone {
        width: 100%;
        height: 100%;
        color: #fff;
        text-align: center;
    }
    .van-icon.van-icon-arrow{
        display: none;
    }
    .van-popup div{
        width: 100%;
        height: 33%;
        text-align: center;
        font-size: 1.25rem;
        line-height: 4.5rem;
    }
    .van-popup input{
        display: none;
    }
    .van-cell__title{
        width: 3rem;
    }
</style>