<template>
    <div class="body">
        <table></table>
        <div class="fanhui1">
            <table></table>
            <img src="../../assets/study_img/03.png" @click="fanhui">
        </div>
        <div class="tou1"  v-for="(item,i) of list" :key="i">
            <div class="ab1">
                <img :src="item.bookpic" alt="">
            </div>
            <div class="ab2">
                <h3>{{item.bname}}</h3>
                <span>作者: {{item.author}}</span>
            </div>
        </div>
        <div class="neirong1">
            <span>
                月光如流水一般，静静地泻在这一片叶子和花上。薄薄的青雾浮起在荷塘里。叶子和花仿佛在牛乳中洗过一样；又像笼着轻纱的梦。虽然是满月，天上却有一层淡淡的云，所以不能朗照；但我以为这恰是到了好处——酣眠固不可少，小睡也别有风味的。月光是隔了树照过来的，高处丛生的灌木，落下参差的斑驳的黑影，峭楞楞如鬼一般；弯弯的杨柳的稀疏的倩影，却又像是画在荷叶上。塘中的月色并不均匀；但光与影有着和谐的旋律，如梵婀玲⑼上奏着的名曲。
            </span>
        </div>
        
        <div>
            <ul>
                <h4>目录</h4>
                <li v-for="(item,i) of list2" :key="i">{{item}}</li>
            </ul>
        </div>
        <div class="denglu1">
            <button v-if="!islogin" @click="denglu">立 即 登 录</button>
            <button v-else @click="addbooks">加入书架</button>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            list:[],
            list2:[],
            bid:"",
            islogin:false
        }
    },
    created() {
        //console.log(this.$route)
        this.bid = this.$route.query.bid;
        //console.log(bid);
         this.loadMore();
        if(this.$store.getters.getUser.uid!=undefined){
            this.user = this.$store.getters.getUser;
            console.log(this.user)
            this.islogin=true;
        }
    },
    methods: {
        fanhui(){
            history.go(-1);
        },
        denglu(){
            this.$router.push("/login");
        },
        loadMore(){
            var url = "/v2/details";
             
            this.axios.get(url,{params:{bid:this.bid}}).then(
                res=>{
                   
                    this.list=res.data;
                    // console.log(this.list);
                    // console.log(this.list[0].list)
                    this.list2 = this.list[0].list.split("/")
                    console.log(this.list2)
                }
            )
        },
        addbooks(){
    
            this.axios.get("/addbooks",{params:{bid:this.bid}}).then(res=>{
                this.$toast("加入成功")
            })
        }
    },
}
</script>
