<template>
  <div class="myxqq">
  <!-- 下拉刷新 -->
    <div class="fiexd">
      <van-nav-bar title="" left-text="" left-arrow @click-left="onClickLeft"/>
      <div @click="add" class="iconfont icon-gengduo myxq-ico"></div>
      <van-action-sheet
      v-model="show"
      cancel-text="取消"
      :actions="actions"
      @select="onSelect"/>
    </div>
  <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
  </van-pull-refresh>
    <div style="height:40px;"></div>
      <div class="myxq">
        <!-- 标题 -->
        <h4 class="myxq-h">{{lists.title}}</h4>
        <!-- 作者 -->
        <div class="myxqd">
          <van-row>
          <van-col span="22"> 
          <div style="height:30px;width:30px;display:inline-block;">
          <img :src="'http://127.0.0.1:4000/'+lists.usepic" alt="" class="myxqd-i">
          </div>
          <span class="myxqd-d">{{lists.names}}</span><span class="myxqd-d">({{luo}})</span>
          </van-col>
          <van-col span="2"><div class="myxqd-di">{{lists.type}}分</div></van-col>
          </van-row>
          <!-- 文本区 -->
          <div class="myxq-div">
            <div>{{lists.content}}</div>
            <!-- <img :src="item" v-for="(item,i) of imgss" :key="i" class="myxq-img"/> -->
          </div>
          <div class="myxq-zan">
          <van-row type="flex">
          <van-col span="18"><span>{{fun.timesFun(lists.time).timesString}}</span><span class="huifu">回复</span></van-col>
          <van-col span="6"><span @click="jubao">举报</span><span class="iconfont icon-zan huifu" @click="zan" :class="run"></span><span>{{lists.star==0?"赞":lists.star}}</span></van-col>
          </van-row>
          <!-- 弹出框 -->
           <van-action-sheet
            v-model="showw"
            cancel-text="取消"
            :actions="actionss"
            @select="onSelects"/>
          </div>
           
        </div>
        
      </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      run:{fan:false},
      isLoading: false,
      show: false,
      actions: [{ name:"正序" },],
      showw:false,
      actionss: [
        { name: '其他'},
        { name: '色情'},
        { name: '政治'},
        { name: '广告' },
        { name: '招聘'},
        { name: '刷屏'},
        { name: '倒分' },
        { name: '灌水'},
        { name: '骂人'},
        { name: '跑题'}],
        luo:"楼主",
       
        lists:{time:"",usepic:"js_01.png",names:"",content:"",type:"",title:""}
    }
  },

  methods: {
    zan(){
      this.run.fan=!this.run.fan
      if(this.run.fan==true){
       this.lists.star++
      }else{
        this.lists.star--
      }
    },
    //下拉刷新
     onRefresh() {
      setTimeout(() => {
      this.$toast('刷新成功');
      this.isLoading = false;
      }, 500);
    },
    onClickLeft() {
     this.$router.go(-1);
    },
    // 更多的底部弹出框
    add(){
    this.show=true;
    },
    onSelect(item) {
      // 点击选项时默认不会关闭菜单，可以手动关闭
      this.show = false;
       if(item.name=="正序"){
         item.name="降序"
         }else if(item.name=="降序")
        {item.name="正序"}
    //  console.log(item.name)
   },
   //举报的底部弹出框
   jubao(){
    this.showw=true;
     },
    onSelects(item){
       this.showw = false;
       if(item.name){
         this.$toast('感谢您的举报，我们会尽快处理');
       }
    },
  load(){
    var url="/v4/lq"
    var obj={tid:this.tid};
    this.axios.get(url,{params:obj}).then(res=>{ 
    this.lists=res.data.data[0];
    //console.log(lists.time)
    //console.log(this.lists)
    })
}
    
  },
  props:["tid"],
  created(){
    this.load();
  },
  watch:{
    tid(){
      this.load()
    }
  }
}
</script>
<style scoped>
.fiexd{
  position: fixed;
}
.van-action-sheet__item{
  height: 40px;
}
.myxq-ico{
  font-size: 25px;
  position: fixed;
  top:5px;
  right: 6px;
  z-index: 1;
}
.van-nav-bar__arrow{
  font-size: 25px;
}
.myxq .myxq-h{
  background: #eee;
  padding:15px;
  margin:0;
  line-height:22px;
  
}
.myxq .myxqd{
  margin-top:10px;
  font-size:8px;
  margin-left: 15px;
  
  
}
.myxq .myxqd .myxqd-i{
  width: 100%;
  border-radius:50%;
  height:100%; 
  vertical-align:text-top;
  margin-right: 5px;
}
.myxq .myxqd .myxqd-d{
  margin-left:5px;

}
.myxq .myxqd .myxqd-di{
  color:darkorange;
}
.myxq-div{
  margin-top:5px;
  margin-left: 30px;
  font-size: 14px;
}
.myxq-img{
  margin-top:10px;
  width: 100%;
  
}
.myxq-zan{
  margin-left: 15px;
  margin-top:10px;
  color: #aaa;

}
.huifu{
  margin: 0 5px 0 20px;
  
}
.fan{
  color: #f00;
}
</style>