<script>
//var a=888;
import Index from './index'
// import cs from '../../assets/js/cs01'
export default {
    data(){
        return {
            all:"all",
            concern:"concern",
            selected:"1",
            wi:{marginLeft:"0px"},
            x:"",
            b:"",
            list:[],
            classes:[],
        }
    },
    methods:{
        login(){
            this.$router.push('/seach')
        },
        start(e){
            this.x=e.touches[0].clientX;
        },
        move(e){
            var a=parseFloat(this.wi.marginLeft)
            if(this.b==""){this.b=this.x;} 
            var x1=e.touches[0].clientX;
            //console.log(x1) ;           
            var w=x1-this.b;
            this.b=x1;
            //console.log(w);  
            a=a+w; 
            var width = this.classes.length*60+80;
            if(a<=0&&a>=-1*width){
            this.wi.marginLeft=a+"px";         
            //console.log(parseInt(this.wi.marginLeft))
            }else if(a>=0){
                this.wi.marginLeft="0px";
            }else if(a<=-1*width){
                this.wi.marginLeft=-1*width+"px";
            }
        },
        end(e){
            this.b="";
        },
       
    },
    components:{
        "index":Index
    },
    created(){
        if(this.$store.getters.getUser.uid==undefined){
            this.$toast("未登录状态")
            return;
        }else{
            this.axios.get("/class").then(res=>{
                this.classes = res.data.msg[0].classes.split(",");
                console.log(this.classes)
            })
        }
    }
}
</script>
