<template>
    <div>
        <myHeader :isbar='isbar'  :title="title" :tab="tab"></myHeader>
        <main>
            <div id="form">
                <div></div>
                <table>
                    <tr>
                        <td> <input type="text" placeholder="用户名" v-model="uname"></td>
                    </tr>
                    <tr>
                        <td> <input type="password" placeholder="密码" v-model="upwd"></td>
                    </tr>
                </table>
            </div>           
            <button id="login" @click="login">登 录</button>
            <router-link to="/reg" id="forget">注册账户</router-link>
            <div id="others">
                <p>第三方快速注册/登录</p>
                <button><img src="../../assets/微信.png" alt=""><span>微 信</span></button>
                <button><img src="../../assets/qq.png" alt=""><span>Q Q</span></button>
            </div>
            <p id="bottom">注册 / 登录即代表同意<router-link to="/rules">《用户服务条款》</router-link>与<router-link to="/conceal">《隐私协议》</router-link></p>
        </main>
    </div>
</template>
<script>
import myHeader from './myheader'
export default {
    data() {
        return {
            title:["登录"],
            tab:{code:1},
            isbar:false,
            uname:"",
            upwd:"",
        }
    },
    methods:{
        login(){
             if(!/^\w+$/.test(this.uname)){ this.$messagebox.alert("用户名格式错误");return}
            if(!/^\d+$/.test(this.upwd)){this.$messagebox.alert("密码格式错误");return}
            this.axios.get('http://127.0.0.1:4000/login',{
                params:{
                    uname:this.uname,
                    upwd:this.upwd 
                }
            }).then(result=>{
                console.log(result);
                if(result.data.code == 1){
                    this.$messagebox.alert("登录成功")
                    this.uname = result.data.msg[0].uname;
                    var user = result.data.msg[0];
                    this.$store.commit('saveUser',user)
                    
                    console.log(this.$store.getters.getUser)
                }else{
                    this.$messagebox.alert("登录失败")
                }
            }).then(()=>{
                this.$router.push('/index')
            })
        }
        
    },
    components:{
        myHeader,
    }
}
</script>
<style scoped>
    *{
        padding: 0;
        margin: 0;
    }
   
    #form{
        width: 80%;
        margin: 1rem auto;
        background-image: url('../../assets/程序猿.jpg');
        background-repeat: no-repeat;
        background-position: center;
    }
    #form>div{
        width: 100%;
        height: 3rem;
    }
    a#forget{
        float: right;
        margin-right: 2rem;
        font-size: 0.75rem;
    }
    table{
        width: 100%;
        height: 6rem;
        margin: 3rem auto 1rem;
        border:1px solid red;
        border-radius: 0.75rem;
        background-color:#fff;
        overflow: hidden;
    }
    table td input{
        width: 90%;
        height: 100%;
        padding:0;
        border:none;
        padding-left: 2.5rem;
        background-image: url('../../assets/密码.png');
        background-size: 10%;
        background-repeat: no-repeat;
        background-position: 0.1rem center;
    }
    table tr:first-child td input{
        border-bottom: 0.5px solid #aaa;
        background-image: url('../../assets/用户.png');
    }
    #login{
        width: 80%;
        height: 2.5rem;
        margin: 0.5rem auto ;
        background-color: rgb(211, 19, 19);
        border: 0.5px solid #ccc;
        border-radius: 0.5rem; 
        color: #6cf;
    }
    #others{
        position: relative;
        margin-top:8rem;
    }
    #others button{
        width: 80%;
        height: 2.5rem;
        margin: 1rem auto 0;
        background-color: #fff;
        border: 0.5px solid #ccc;
        border-radius: 0.5rem; 
        text-align: center;       
        padding: 0.3rem;
        
    }
    #others button img{
        height: 100%;
        margin: 0 0.5rem;
        vertical-align: middle;
    }
    #others button:nth-child(2){
        color:#3bb034
    }
    #others button:last-child{
        color:#16acfe
    }
    #bottom{
        position: absolute;
        font-size:0.75rem;
        padding-left: 10%;
        bottom: 1rem;
    }
    #bottom a{
        color:#000;
        text-decoration:underline;
    }
</style>