<template>
    <div id="container">
        <mt-tab-container v-model="active">
            <mt-tab-container-item id="tab1">
                <home></home>
            </mt-tab-container-item>
            <mt-tab-container-item id="tab2">
                <study></study>
            </mt-tab-container-item>
            <mt-tab-container-item id="tab3">
                <blink></blink>
            </mt-tab-container-item>
            <mt-tab-container-item id="tab4">
                <forum></forum>
            </mt-tab-container-item>
            <mt-tab-container-item id="tab5">
                <center></center>
            </mt-tab-container-item>
        </mt-tab-container>
        <mt-tabbar v-model="active" fixed @click.native="chgact">
            <mt-tab-item id="tab1">
                <TabbarIcon :norImg="require('../assets/home.png')"
                            :selImg="require('../assets/home1.png')"
                            :focused="active=='tab1'"></TabbarIcon>
                首页
            </mt-tab-item>
            <mt-tab-item id="tab2">
                <TabbarIcon :norImg="require('../assets/学习.png')"
                            :selImg="require('../assets/学习1.png')"
                            :focused="active=='tab2'"></TabbarIcon>
                学习
            </mt-tab-item>
            <mt-tab-item id="tab3">
                <TabbarIcon :norImg="require('../assets/web_link.png')"
                            :selImg="require('../assets/web_link1.png')"
                            :focused="active=='tab3'"></TabbarIcon>
                Blink
            </mt-tab-item>
            <mt-tab-item id="tab4">
                <TabbarIcon :norImg="require('../assets/论坛.png')"
                            :selImg="require('../assets/论坛1.png')"
                            :focused="active=='tab4'"></TabbarIcon>
                论坛
            </mt-tab-item>
            <mt-tab-item id="tab5">
                <TabbarIcon :norImg="require('../assets/猴头.png')"
                            :selImg="require('../assets/猴头1.png')"
                            :focused="active=='tab5'"></TabbarIcon>
                我的
            </mt-tab-item>
        </mt-tabbar>
    </div>
</template>
<script>
import TabbarIcon  from './common/TabbarIcon'
import center  from './common/center'
import home  from './home/shouye'
import study  from './study/study'
import forum  from './comment/Forum'
import blink  from './blink/Blink'
export default {
    data() {
        return {
            search:"",
            add:"",
            active:"tab1",
            img:"../assets/home (1).png"
        }
    },
    created() {
        this.active=sessionStorage.getItem("active")
    },
    methods:{
        chgact(e){
            console.log(1);
            if(e.target.innerText=="微信"){
                this.active = "tab1"
                
            }else if(e.target.innerText=="通讯录"){
                this.active = "tab2"
            }else if(e.target.innerText=="发现"){
                this.active = "tab3"
            }else if(e.target.innerText=="我"){
                this.active = "tab4"
            }
            sessionStorage.setItem("active",this.active);
        }
    },
    components:{
        TabbarIcon,
        center,
        home,
        study,
        forum,
        blink
    }
}
</script>
<style scoped>
    #container{
        margin-bottom: 5rem;
    }
</style>